.navigation {
  position: fixed;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-top: 2rem;
  color: #ffffff;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid #ffffff;
  right: 0;
  left: 0;
  top: 0;
  z-index: 10;
}
.navigation-pages {
  background-color: #000000;
  z-index: 10;
}
.navigation__nav {
  align-self: center;
}
.navigation__list {
  display: flex;
  flex-direction: row;
  list-style-type: none;
  font-size: 1rem;
  font-weight: 400;
}
.navigation__item {
  margin-right: 3rem;
}
.navigation__item:hover {
  text-decoration-line: underline;
}
.navigation__link {
  text-decoration: none;
  color: #ffffff;
  font-size: 1.3rem;
}
.logo-navigation {
  align-self: flex-end;
  font-family: "didot";
  font-size: 2.5rem;
  color: #ffffff;
  text-decoration: none;
}
.logo {
  margin: 0 10rem 0 2rem;
  display: flex;
}
.cartNo {
  position: absolute;
  color: #ffffff;
  /* left: 30%;
  top: 50%; */
  margin-left: 1rem;
  margin-top: 0.4rem;
  font-size: 1rem;
}

.navbtn {
  display: none;
  position: absolute;
  right: 1rem;
  top: 2.2rem;
  background-color: transparent;
  border-style: none;
}
.navsvg {
  fill: #ffffff;
}
@media screen and (max-width: 500px) {
  .navigation {
    flex-direction: column;
  }
  .navbtn {
    display: block;
  }
}
